.burger {
  display: none;
}

input + label {
  position: fixed;
  top: 40px;
  right: 40px;
  height: 20px;
  width: 15px;
  z-index: 5;
  span {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    margin-top: -1px;
    left: 0;
    display: block;
    background: #000;
    transition: 0.5s;
  }
  span:first-child {
    top: 3px;
  }
  span:last-child {
    top: 16px;
  }
}

label:hover {
  cursor: pointer;
}

input:checked + label {
  span {
    opacity: 0;
    top: 50%;
  }
  span:first-child {
    opacity: 1;
    transform: rotate(405deg);
  }
  span:last-child {
    opacity: 1;
    transform: rotate(-405deg);
  }
}

input ~ nav {
  background: #0075ff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 3;
  transition: 0.5s;
  transition-delay: 0.5s;
  overflow: hidden;
  > ul {
    text-align: center;
    position: absolute;
    top: 35%;
    left: 20%;
    right: 20%;
    > li {
      opacity: 0;
      transition: 0.5s;
      transition-delay: 0s;
      list-style: none;
      > a {
        text-decoration: none;
        text-transform: uppercase;
        color: #fff;
        font-weight: 700;
        font-family: sans-serif;
        display: block;
        padding: 30px;
      }
    }
  }
}

input:checked ~ nav {
  height: 100%;
  transition-delay: 0s;
  > ul {
    > li {
      opacity: 1;
      transition-delay: 0.5s;
    }
  }
}
